.form
  @apply mx-auto rounded-md overflow-hidden border border-slate-900 max-w-lg
  .input
    @apply w-full h-8 bg-transparent  focus:outline-0 px-3 border-b-0

  .btn
    @apply w-full py-1 bg-slate-800 text-white hover:bg-slate-700 transition-all

.loading_block
  @apply text-center mt-2
